.activity-history-container::-webkit-scrollbar {
  width: 6px;
}
.activity-history-container::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 3px;
}
.activity-history-container::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 3px;
}
.activity-history-container::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

.image-gallery-item {
  transition: transform 0.2s ease-in-out;
}
.image-gallery-item:hover {
  transform: scale(1.02);
}

.modal-backdrop {
  backdrop-filter: blur(4px);
}
